
import { Options, Vue } from 'vue-class-component'

@Options({
	name: 'DetailsContent',
	methods: {
		getImg(path: string, extension: string) {
			return path + '.' + extension
		},
	},
	computed: {
		storeDetails() {
			return this.$store.state.details
		},
	},
})
export default class DetailsContent extends Vue {}
